import React, {Component} from 'react'
import Progress from "./Progress"
import Quote from './Quote';
import Resume from "../pdf/Resume.pdf"
import Patreon from "../icons/patreon.png"

class Home extends Component {
    render(){
        const {parentHeight} = this.props;
        const style = {
            height: parentHeight,
            paddingTop: "0.5%"
        }
        console.log(style);
        return (
            <React.Fragment>
                <div className="row" style={style}>
                    <div className="col s12 m4 l4 blue-grey darken-3 hovering" >
                        <div className="row">
                            <div className="col l12 m12 s12 valign-wrapper">
                                <Quote/>
                            </div>
                            <div className="col l12 s12 m12" >
                                <h4 className="center white-text">Skills</h4>
                                <Progress/>
                            </div>
                        </div>
                        <div className="row ">
                            <div className="col s12 m12 l12 left-align" style={{display: "flex",  flexWrap: "wrap"}}>
                                <h4 className="white-text">Accomplishments</h4>
                                <div className="row">
                                    <div className="col l4">
                                        <div className="card-panel teal lighten-1">
                                            <span className="white-text flow-text">Most Creative Team for Shopee National Data Science Challenge 2019
                                            </span>
                                        </div>
                                    </div>
                                    <div className="col  l4">
                                        <div className="card-panel deep-orange lighten-2">
                                            <span className="white-text flow-text">Champion Team for Yitu Hack-AI-thon challenge 2019
                                            </span>
                                        </div>
                                    </div>
                                    <div className="col l4">
                                        <div className="card-panel amber darken-4">
                                            <span className="white-text flow-text">Top Five Teams for Shopee Best Coder Challenge 2019
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                            <div className="col s12 m12 l12 right-align">
                            <p style={{padding:"2%", paddingTop:"5%"}} className="flow-text \"><span className="white-text home-span">Download my resume: </span><a href = {Resume} download className="btn-floating btn-large blue waves-effect"><i className="material-icons">cloud_download</i></a></p>
                            <p style={{padding:"2%"}} className="flow-text"><span className="white-text home-span">Patreon! <font color="#FA8072"><i>Coming Soon!</i></font> </span><a href = {Resume} download className=" btn-floating disabled btn-large white center"><img src= {Patreon} className = "icon-small"/></a></p>
                                
                            </div>
                        </div>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }
}

export default Home